(function(){
    var app = angular.module('app');
    
    //2. Update configuration
    app.config(['$stateProvider',function($stateProvider) {
       
        //2.5 Angular Router
        $stateProvider
            .state('supplierDashboard',{
                url: '/supplier-dashboard',
                // abstract: true,
                views: {
                    'topHeader': {
                        templateUrl: 'assets/partials/dashboard/base/top-header.html',
                        controller: 'dashboard.controller'
                    },
                    'sideNavigation': {
                        templateUrl: 'assets/partials/dashboard/supplier/side-navigation.html'
                    },
                    'container': {
                        templateUrl:'assets/partials/dashboard/base/container.html'
                    }
                }
            })
            .state('supplierDashboard.summary',{
                url: '/summary',
                templateUrl: 'assets/partials/dashboard/supplier/summary.html'
            })
            .state('supplierDashboard.inventory',{
                url: '/inventory',
                templateUrl: 'assets/partials/dashboard/supplier/inventory.html',
                controller: 'dashboard.supplier.inventory'
            })
            .state('supplierDashboard.directory',{
                url: '/directory',
                templateUrl: 'assets/partials/dashboard/supplier/directory.html',
                controller: 'dashboard.supplier.directory'
            })
            .state('supplierDashboard.team',{
                url: '/team',
                templateUrl: 'assets/partials/dashboard/supplier/team.html'
            });
    }]);

})();