(function(){
    var app = angular.module('app');
    
    //2. Update configuration
    app.config(['$stateProvider','$urlRouterProvider',function($stateProvider,$urlRouterProvider) {
       
        //2.5 Angular Router
        $stateProvider
            .state('inventory',{
                url: '/inventory',
                abstract: true,
                redirectTo: 'inventory.summary.category',
                templateUrl: '/assets/js/modules/inventory/main.html'
            })
            .state('inventory.summary',{
                url: '/summary',
                abstract: true,
                templateUrl: 'assets/js/modules/inventory/states/summary/main.html',
            })
            .state('inventory.summary.category',{
                url: '/category',
                templateUrl: 'assets/js/modules/inventory/states/summary/states/category/main.html',
                controller: 'inventory.summary.categoryController',
                resolve: {
                    loadMyCtrl: ['$ocLazyLoad', function($ocLazyLoad) {
                        return $ocLazyLoad.load('assets/js/modules/inventory/states/summary/states/category/main.js');
                     }]
                }
            })
            .state('inventory.list',{
                url: '/list',
                abstract: true,
                templateUrl: '/assets/partials/inventory/main.html',
                controller: 'inventoryList.mainController'
            })
            .state('inventory.list.category',{
                url: '/category/:categoryId',
                views: {
                    results: {
                        templateUrl: '/assets/partials/inventory/category/results.html',
                        controller: 'inventoryList.category.results'        
                    },
                    filters: {
                        templateUrl: '/assets/partials/inventory/filters.html',
                        controller: 'inventoryList.filters'          
                    }
                }
            });
        $urlRouterProvider
            .when('/inventory/','inventory/summary/category')
            .when('/inventory','inventory/summary/category')
    }]);

})();