(function(){
    var app = angular.module('app');
    
    //2. Update configuration
    app.config(['$stateProvider',function($stateProvider) {
       
        //2.5 Angular Router
        $stateProvider
            .state('adminDashboard',{
                url: '/admin-dashboard',
                // abstract: true,
                views: {
                    'topHeader': {
                        templateUrl: 'assets/partials/dashboard/base/top-header.html',
                        controller: 'dashboard.controller as vm'
                    },
                    'sideNavigation': {
                        templateUrl: 'assets/partials/dashboard/admin/side-navigation.html'
                    },
                    'container': {
                        templateUrl:'assets/partials/dashboard/base/container.html'
                    }
                }
            })
            .state('adminDashboard.user',{
                url: '/user',
                templateUrl: 'assets/partials/dashboard/admin/user.html'
            })
            .state('adminDashboard.inventory',{
                url: '/inventory',
                templateUrl: 'assets/partials/dashboard/admin/inventory.html',
                controller: 'dashboard.admin.inventory'
            })
            .state('adminDashboard.parentCategory',{
                url: '/parentCategory',
                templateUrl: 'assets/partials/dashboard/admin/parentCategory.html'
            })
            .state('adminDashboard.city',{
                url: '/city',
                templateUrl: 'assets/partials/dashboard/admin/city.html'
            })
            .state('adminDashboard.newsletter',{
                url: '/newsletter',
                templateUrl: 'assets/partials/dashboard/admin/newsletter.html'
            })
            .state('adminDashboard.category',{
                url: '/category',
                templateUrl: 'assets/partials/dashboard/admin/category.html'
            })
            .state('adminDashboard.directory',{
                url: '/directory',
                templateUrl: 'assets/partials/dashboard/admin/directory.html'
            })
            .state('adminDashboard.industry',{
                url: '/industry',
                templateUrl: 'assets/partials/dashboard/admin/industry.html'
            })
            .state('adminDashboard.manufacturer',{
                url: '/manufacturer',
                templateUrl: 'assets/partials/dashboard/admin/manufacturer.html'
            })
            .state('adminDashboard.supplier',{
                url: '/supplier',
                templateUrl: 'assets/partials/dashboard/admin/supplier.html'
            });
    }]);

})();