(function(){
    var app = angular.module('app');
    
    //2. Update configuration
    app.config(['$stateProvider',function($stateProvider) {
       
        //2.5 Angular Router
        $stateProvider
            .state('buyerDashboard',{
                url: '/buyer-dashboard',
                // abstract: true,
                views: {
                    'topHeader': {
                        templateUrl: 'assets/partials/dashboard/base/top-header.html',
                        controller: 'dashboard.controller'
                    },
                    'sideNavigation': {
                        templateUrl: 'assets/partials/dashboard/buyer/side-navigation.html'
                    },
                    'container': {
                        templateUrl:'assets/partials/dashboard/base/container.html'
                    }
                }
            })
            .state('buyerDashboard.shortlist',{
                url: '/shortlist',
                templateUrl: 'assets/partials/dashboard/buyer/shortlist.html'
            })
            .state('buyerDashboard.message',{
                url: '/message',
                templateUrl: 'assets/partials/dashboard/buyer/message.html'
            });
    }]);

})();