(function(){
    var app = angular.module('app');
    
    //2. Update configuration
    app.config(['$stateProvider','$urlRouterProvider',function($stateProvider,$urlRouterProvider) {
       
        //2.5 Angular Router
        $stateProvider
            .state('register',{ // Loads Register Module
                url: '/register',
                abstract: true,
                templateUrl: '/assets/js/modules/register/register.html',
                controller: 'register.controller',
                resolve: { 
                    loadMyCtrl: ['$ocLazyLoad', function($ocLazyLoad) {
                             return $ocLazyLoad.load([
                                    '/assets/js/modules/register/register.js',
                                    '/assets/js/services/user-service.js' // ensures that user service is avaialble for all siblings
                                ]);
                    }]
                }
            })
            .state('register.options',{
                url: '/options',
                controller: 'register.options.step1.controller',
                templateUrl: '/assets/js/modules/register/states/options/step-1/main.html',
                resolve: { 
                    loadMyCtrl: ['$ocLazyLoad', function($ocLazyLoad) {
                             return $ocLazyLoad.load('/assets/js/modules/register/states/options/step-1/main.js');
                    }]
                }
            })
            .state('register.default',{
                url: '/default',
                abstract: true,
                template: '<div ui-view></div>'
            })
            .state('register.default.step2',{
                url: '/step2?userId',
                controller: 'register.default.step2.controller',
                templateUrl: '/assets/js/modules/register/states/default/step-2/main.html',
                resolve: {
                    loadMyCtrl: ['$ocLazyLoad', function($ocLazyLoad) {
                             return $ocLazyLoad.load('/assets/js/modules/register/states/default/step-2/main.js');
                    }]
                }
            })
            .state('register.social.step2',{
                url: '/step2',
                templateUrl: '/assets/js/modules/register/states/social/social-step-2/main.html'
            })
            .state('register.social.step3',{
                url: '/step3',
                templateUrl: '/assets/js/modules/register/states/social/social-step-3/main.html'
            });
        $urlRouterProvider
            .when('/register/','register/options')
            .when('/register','register/options')
    }]);

})();