(function(){
    angular.module('app')
   .controller('AppGlobalController',[
    '$scope',
    '$http',
    '$timeout',
    '$ocLazyLoad', 
    '$injector',
    function($scope,$http,$timeout,$ocLazyLoad, $injector){
        // NgProgressFactory
        $ocLazyLoad.load('/assets/js/bower/ngprogress.js').then(function() { // Consider ngProgress module is loaded
            var ngProgressFactory = $injector.get('ngProgressFactory');
            $scope.progressbar = ngProgressFactory.createInstance();
            $scope.progressbar.start();
            $timeout(function() {
               $scope.progressbar.complete();
            }, 3000);
        });
        
        $ocLazyLoad.load('/assets/js/services/user-service.js').then(function() {
          var UserService = $injector.get('UserService');
          // --- CENTRAL LOGIN SECTION ---
          // Find current user
          $scope.current_user = {}; // Parent Scope.
          $scope.current_user.token = UserService.getDataFromLocalStorage('token');
          UserService.getCurrentUser().then(function(data){
            // Assign attributes to scope
            $scope.current_user.data = data.data;
          }).catch(function(response){
          });
          // Login & Logout Functionality
          $scope.current_user.logout = function(route){
            UserService.deleteTokenFromLocalStorage();
            UserService.logout().then(function(data){
              if(route){$location.url(route);};
              $window.location.reload();  
            },function(err){
            })
          };
          $scope.current_user.login = function(data, valid_data){
            console.log(data);
            var Notification = $injector.get('Notification');
            $scope.current_user.unsuccessful_attempt = false;
            if(valid_data){
              UserService.getToken(data.username, data.password).then(function(response){
                Notification.success({
                  message:'Successfully LoggedIn',
                  positionX:'right',
                  positionY:'bottom'
                });
                $scope.current_user.token = response.data.token; // from main controller
                var success = UserService.saveTokenToLocalStorage($scope.current_user.token);
                if(success){
                  $window.location.reload();
                }else{
                };
              }).catch(function(response){
                $scope.current_user.unsuccessful_attempt = true;
              });
             }else{
            };
          };
        });
        
   }]);
})();